<template>
  <nav class="navbar fixed-top navbar-dark bg-dark" aria-label="First navbar example">
    <div class="container-fluid d-flex">
      <div class="flex-fill">
        <button @click="backToLang()" class="navbar-toggler collapsed border-0 px-0">
          <i class="fa fa-language fs-5" aria-hidden="true"></i>
        </button>
      </div>
      <div v-if="!isClicked" class="flex-fill d-flex justify-content-center align-items-center">
       <button class="btn d-flex" @click="redirectToMap(isMap)">
         <i v-if="!isMap" class="far fa-map text-white fs-5"></i>
         <i v-else class="fas fa-home text-white fs-5"></i>
       </button>
      </div>
      <div class="flex-fill">
        <a class="navbar-brand mx-0 fw-bold" @click="setHomePage();">1944 Monte Cassino</a>
      </div>
      <div class="flex-fill" v-if="!isMap && !firstMenu">
        <button @click="changeVisible(isClicked,1,lastMenu)" class="navbar-toggler collapsed border-0 px-0" type="button" data-bs-toggle="collapse"
                data-bs-target="#navbarsExample02" aria-controls="navbarsExample01" aria-expanded="false"
                aria-label="Toggle navigation">
          <i class="fas fa-journal-whills fs-5"></i>
        </button>
      </div>
      <div class="flex-fill" v-if="!isMap && !lastMenu">
        <button @click="changeVisible(isClicked,0,firstMenu)" class="navbar-toggler collapsed border-0 px-0" type="button" data-bs-toggle="collapse"
                data-bs-target="#navbarsExample01" aria-controls="navbarsExample01" aria-expanded="false"
                aria-label="Toggle navigation">
          <span class="navbar-toggler-icon fs-5"></span>
        </button>
      </div>
      <div class="navbar-collapse collapse mobileMenu" id="navbarsExample01">
        <ul class="navbar-nav me-auto mb-2 d-flex h-100 ">
          <li v-for="(page,key) in pagesNavbar" :key="key" class="nav-item text-white flex-fill d-flex justify-content-center align-items-center">
            <a v-smooth-scroll :href="page.link" @click="removeShow()">
              {{page.title}}
              </a>
          </li>
        </ul>

      </div>
      <div class="navbar-collapse collapse mobileMenu" id="navbarsExample02">
        <ul class="navbar-nav me-auto mb-2 d-flex h-100 ">
          <li v-for="(topPage,key) in topPages" :key="key" class="nav-item text-white flex-fill d-flex justify-content-center align-items-center">
            {{topPage.title}}
          </li>
        </ul>

      </div>

    </div>
  </nav>
</template>
<script>
export default {
  props:['pagesNavbar','topPages'],
  data() {
    return {
      isMap: false,
      isClicked:false,
      firstMenu:false,
      lastMenu:false
    }
  },
  methods: {
    redirectToMap(value) {
      this.isMap = !value;
      this.$store.commit('setMobileMap',this.isMap );
      this.runPreloader();
    },
    changeVisible(value,menu,typeMenu){
      this.isClicked = !value;
      if(menu == 1){
        this.lastMenu =!typeMenu
      } else {
        this.firstMenu = !typeMenu
      }
    },
    backToLang(){
      let $ = require('jquery');
      $(location).attr('href', '/')
    },
    removeShow(){
      let $ = require('jquery');
      $('.mobileMenu').removeClass('show');
    },
    setHomePage(){
      if(this.isMap){
        this.$store.commit('setMobileMap',false );
        this.runPreloader();
      }
    },
    runPreloader() {
      let loader = this.$loading.show(
              {
                backgroundColor: '#000',
                opacity: 1,
                color: '#fff',
                loader: 'dots'
              }
      );
      setTimeout(() => {
        loader.hide();
      }, 1000);
    }
  },
  watch: {
    '$store.state.isMobileMap': function (isMobile) {
      this.isMap = isMobile
    },
  }
}
</script>
<style scoped>
.mobileMenu{
  height: 95vh!important;
}
.mobileMenu a {
  text-decoration: none;
  color:white;
}
.navbar-toggler {
  border-color: transparent !important;
}
</style>